<template>
	<view class="mask" catchtouchmove="move">
		<view class="mask-dialog">
				<view class="mask-dialog-content">
					{{showText}}<slot name="after"></slot>
				</view>
				<view class="mask-dialog-btn single" v-if="isShowAuthLocationBtn">
					
					<button class="btn" style="padding: 0; margin: 0;background: transparent; border: 1upx solid #fff; border-radius: 18upx; height: 90upx" open-type="openSetting" bindopensetting="callback">去开启</button>
				</view>
				<view class="mask-dialog-btn" v-else>
					<view class="cancel" @click='cancelBtn()'>{{cancelText}}</view>
					<view class="confirm" @click='confirmBtn()'>{{confirmText}}</view>
				</view>
		</view>
	</view>
</template>

<script>
	export default {
		// 弹窗
		name: 'popDialog',
		props: {
			showText: {
				type: String,
				default: ''
			},
			cancelText: {
				type: String,
				default: '取消'
			},
			confirmText: {
				type: String,
				default: '确认'
			},
			isShowAuthLocationBtn: {
				type: Boolean,
				default: false
			}
		},
		data () {
			return  {
				
			}
		},
		mounted () {
			
		},
		methods:{
			callback (e) {
				console.log('0000000000000000000000000')
				console.log(e)
				this.$emit('confirmBtnLocation', e)
			},
			cancelBtn () {
				this.$emit('cancelBtn')
			},
			confirmBtn () {
				this.$emit('confirmBtn')
			},
			move () {
				return
			},
		}
	}
</script>

<style lang="scss" scoped>
	/* pages/components/tipDialog/tipDialog.wxss */
	.mask {
	  position: fixed;
	  z-index: 9999999;
	  top: 0;
	  left: 0;
	  width: 100%;
	  height: 100%;
	  background: rgba(0, 0, 0, 0.5);
	  display: flex;
	  justify-content: center;
	  align-items: center;
	}
	.mask-dialog {
	  width: 520upx;
	  height: 323upx;
	  background: #fff;
	  border-radius: 18upx;
	}
	.mask-dialog-content {
	  height: 234upx;
	  font-size: 32upx;
	  font-weight: bold;
	  color: #333;
	  display: flex;
	  flex-direction: column;
	  justify-content: center;
	  align-items: center;
	  border-bottom: 1upx solid #dbdbdb;
	  padding: 0 30upx;
	  box-sizing: border-box;
	  text-align: center;
	}
	.mask-dialog-btn {
	  display: flex;
	  flex-direction: row;
	  height: 87upx;
	  &.single {
		  align-items: center;
		  .btn {
			  width: 100%;
			  color: #fe5f5c;
			  justify-content: center;
		  }
	  }
	}
	.mask-dialog-btn .cancel, .mask-dialog-btn .confirm {
	  width: 50%;
	  height: 100%;
	  display: flex;
	  justify-content: center;
	  align-items: center;
	  font-size: 32upx;
	  font-weight: 400;
	}
	.mask-dialog-btn .confirm {
	  border-left: 1upx solid #dbdbdb;
	  color: #fe5f5c;
	}

</style>
